@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Saira+Extra+Condensed:500);
.container {
  height: 100vh;
  position: relative;
  width: 100%;
}

.intro,
.title {
  left: 50%;
  opacity: 0;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  z-index: 200;
}

.intro p {
  color: rgb(75, 213, 238);
  font-size: 1.25rem;
}

.title {
  align-items: center;
  display: flex;
  justify-content: center;
  width: 18rem;
}

/* helps fade the text out as it recedes back in space */
.container:before {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 1)),
    color-stop(20%, rgba(0, 0, 0, 1)),
    color-stop(40%, rgba(0, 0, 0, 0)),
    to(rgba(0, 0, 0, 0))
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 1) 20%,
    rgba(0, 0, 0, 0) 40%,
    rgba(0, 0, 0, 0) 100%
  );
  bottom: 0;
  content: " ";
  left: 0;
  right: 0;
  position: absolute;
  top: 0;
  z-index: 100;
}

.crawl {
  font-size: 300%;
  bottom: 0;
  height: 80rem;
  left: 50%;
  position: absolute;
  -webkit-transform: translateX(-50%) perspective(300px) rotateX(28deg);
          transform: translateX(-50%) perspective(300px) rotateX(28deg);
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  width: 90%;
}

.content {
  position: absolute;
  top: 100%;
}

.episode-number {
  font-weight: 600;
  margin-bottom: 5rem;
  text-align: center;
}

.episode-title {
  font-family: "Saira Extra Condensed", sans-serif;
  font-size: 250%;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 7rem;
  -webkit-transform: scale(1, 1.5);
          transform: scale(1, 1.5);
  text-align: center;
}

.content p {
  font-weight: 700;
  line-height: 1.33;
  margin-bottom: 4rem;
  text-align: justify;
}

.volume {
  background: transparent;
  border: 0;
  bottom: 10px;
  cursor: pointer;
  left: 10px;
  position: absolute;
  z-index: 1000;
}
.volume img {
  height: 24px;
}

